import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useForm, Controller } from "react-hook-form";
import axios from "axios";
import { addDoc, setDoc, onSnapshot, doc } from "@firebase/firestore";
import { dataRef, injectionRef } from "../firebase/firebase";
import { auth, queryGetUserInfoByPhone, db } from "../firebase/firebase";
import { onAuthStateChanged } from "firebase/auth";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Stack from "@mui/material/Stack";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import InputLabel from "@mui/material/InputLabel";
import Typography from "@mui/material/Typography";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import FormHelperText from "@mui/material/FormHelperText";

const Register = () => {
  const navigate = useNavigate();

  const {
    register,
    control,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const [inputEmail, setInputEmail] = useState("");
  // const [inputName, setInputName] = useState("");
  // const [inputDate, setInputDate] = useState("");
  // const [inputAddress, setInputAddress] = useState("");

  const [fetchData, setFetchData] = useState([]);
  const [inputCity, setInputCity] = useState("");

  const [district, setDistrict] = useState([]);
  const [inputDistrict, setInputDistrict] = useState("");

  const [ward, setWard] = useState([]);
  const [inputWard, setInputWard] = useState("");

  onAuthStateChanged(auth, (currentUser) => {
    if (currentUser) {
      setInputEmail(currentUser.email);
    }
  });

  const inputEmailHandler = (e) => {
    setInputEmail(e.target.value);
  };
  // const inputNameHandler = (e) => {
  //   setInputName(e.target.value);
  // };
  // const inputDateHandler = (e) => {
  //   setInputDate(e.target.value);
  // };

  // const inputSubmithandler = async (e) => {
  //   e.preventDefault();

  //   // add data to collection

  //   setInputEmail("");
  //   setInputPhone("");
  //   setInputDate("");
  //   setInputName("");
  //   setInputAddress("");
  // };

  // const phoneHandler = (e) => {
  //   let reg = /(84|0[3|5|7|8|9])+([0-9]{8})\b/;
  //   if (reg.test(e.target.value)) {
  //     setInputPhone(e.target.value);
  //   }
  // };

  const cityHandler = (e) => {
    setInputCity(e.target.value);
    setInputDistrict("");
    setInputWard("");
  };

  const districtHandler = (e) => {
    setInputDistrict(e.target.value);
    setInputWard("");
  };

  const fetchDistrict = async () => {
    const { data } = await axios.get(
      `https://provinces.open-api.vn/api/p/${inputCity}?depth=2`
    );
    return setDistrict(data);
  };

  const fetchWard = async () => {
    if (inputDistrict) {
      const { data } = await axios.get(
        `https://provinces.open-api.vn/api/d/${inputDistrict}?depth=2`
      );
      return setWard(data);
    }
  };

  // console.log(typeof cityName);

  // const fetchExactWard = async

  useEffect(() => {
    axios.get("https://provinces.open-api.vn/api/?depth=1").then((result) => {
      const { data } = result;

      setFetchData(data);
    });
    fetchDistrict();
    fetchWard();
  }, [inputCity, inputDistrict]);

  return (
    <div className="container register">
      <form
        className="register-form"
        onSubmit={handleSubmit((data) => {
          if (district && ward) {
            const updateData = addDoc(dataRef, {
              city: district.name,
              district: ward.name,
              ward: inputWard,
              name: data.inputName,
              idNumber: data.inputId,
              email: inputEmail,
              phone: data.inputPhone,
              dob: data.inputDate,
              address: data.inputAddress,
              assignedRole: "user",
              infected: false,
            });

            // addDoc(injectionRef, {
            //   numberOfInjections: "Chưa tiêm",
            //   phone: data.inputPhone,
            // });

            if (updateData) {
              window.alert("Đăng kí thành công");
              navigate("/");
            }
          }
          // console.log(data, inputWard);
        })}
      >
        <Typography variant="h5" gutterBottom>
          Đăng ký thông tin cá nhân
        </Typography>
        <div>
          <TextField
            sx={{ margin: 1 }}
            id="standard-basic"
            variant="standard"
            label="Email"
            type="text"
            helperText=""
            className="register-email"
            onChange={inputEmailHandler}
            value={inputEmail}
            disabled
          />
        </div>
        <div>
          <TextField
            sx={{ margin: 1 }}
            id="component-error"
            variant="standard"
            label="Họ và tên"
            autoComplete="off"
            // error={inputName === ""}
            id="outlined-error-helper-text"
            // helperText={inputName === "" ? errors.inputName?.message : ""}
            {...register("inputName", { required: "Điền tên" })}
            className="register-name"
          />
          <Typography
            variant="caption"
            display="block"
            gutterBottom
            style={{ color: "red" }}
          >
            {errors.inputName?.message}
          </Typography>
        </div>
        <div>
          <TextField
            sx={{ margin: 1 }}
            id="standard-basic"
            variant="standard"
            className="register-phone"
            label="Số điện thoại"
            autoComplete="off"
            helperText=""
            {...register("inputPhone", {
              pattern: {
                value: /(84|0[3|5|7|8|9])+([0-9]{8})\b/,
                message: "Nhập đúng định dạng số điện thoại",
              },
            })}
          />

          <Typography
            variant="caption"
            display="block"
            gutterBottom
            style={{ color: "red" }}
          >
            {errors.inputPhone?.message}
          </Typography>
        </div>
        <div>
          <TextField
            variant="standard"
            className="register-id"
            id="standard=basic"
            label="CMND/CCCD"
            autoComplete="off"
            helperText=""
            sx={{ margin: 1 }}
            {...register("inputId", { required: "Nhập số CMND hoặc số CCCD" })}
          />
        </div>
        <div>
          <TextField
            sx={{ margin: 2, minWidth: 184 }}
            id="standard-basic"
            helperText="Tháng/Ngày/Năm"
            variant="standard"
            type="date"
            className="register-dob"
            {...register("inputDate", { required: "Nhập ngày tháng năm sinh" })}
          />{" "}
          <Typography
            variant="caption"
            display="block"
            gutterBottom
            style={{ color: "red" }}
          >
            {errors.inputDate?.message}
          </Typography>
        </div>
        <div>
          <FormControl variant="standard" sx={{ m: 2, minWidth: 120 }}>
            <div>
              <InputLabel id="demo-simple-select-label">Thành phố</InputLabel>
              <Select
                sx={{ minWidth: 120 }}
                label={"Thành phố"}
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                className="register-city"
                onChange={cityHandler}
                value={inputCity}
              >
                <MenuItem value="" disabled>
                  Chọn thành phố
                </MenuItem>
                {fetchData &&
                  fetchData.map((val) => (
                    <MenuItem key={val.code} value={val.code}>
                      {val.name}
                    </MenuItem>
                  ))}
              </Select>
            </div>
          </FormControl>
          <FormControl variant="standard" sx={{ m: 2, minWidth: 120 }}>
            <div>
              <InputLabel id="demo-simple-select-label">Quận/Huyện</InputLabel>
              <Select
                sx={{ minWidth: 120 }}
                label={"Quận/Huyện"}
                labelId="demo-simple-select-label1"
                id="demo-simple-select"
                className="register-district"
                onChange={districtHandler}
                value={inputDistrict}
              >
                <MenuItem value="" disabled>
                  Chọn quận huyện
                </MenuItem>
                {district.districts &&
                  district.districts.map((val) => (
                    <MenuItem key={val.code} value={val.code}>
                      {val.name}
                    </MenuItem>
                  ))}
              </Select>
            </div>
          </FormControl>
          <FormControl variant="standard" sx={{ m: 2, minWidth: 120 }}>
            <div>
              <InputLabel id="demo-simple-select-label">Phường/Xã</InputLabel>
              <Select
                sx={{ minWidth: 120 }}
                label={"Phường/Xã"}
                labelId="demo-simple-select-label2"
                id="demo-simple-select"
                className="register-district"
                onChange={(event) => setInputWard(event.target.value)}
                value={inputWard}
              >
                <MenuItem value="" disabled>
                  Chọn phường xã
                </MenuItem>
                {ward.wards &&
                  ward.wards.map((val) => (
                    <MenuItem key={val.code} value={val.name}>
                      {val.name}
                    </MenuItem>
                  ))}
              </Select>
            </div>
          </FormControl>
        </div>
        <div>
          <TextField
            label="Địa chỉ"
            sx={{ margin: 1, marginBottom: 2 }}
            id="standard-basic"
            variant="standard"
            autoComplete="off"
            {...register("inputAddress", { required: "Điền địa chỉ" })}
          />

          <Typography
            variant="caption"
            display="block"
            gutterBottom
            style={{ color: "red" }}
          >
            {errors.inputAddress?.message}
          </Typography>
        </div>
        <div id="sign-in-button"></div>
        <Button variant="contained" type="submit">
          Đăng ký
        </Button>
      </form>
    </div>
  );
};

export default Register;
//onClick={inputSubmithandler}
